* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 12px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@media only screen and (max-width: 768px) {
    .Home {
        flex-direction: column;
    }
}

.Home {
    background-color: #f5f5f5;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
}

.feed {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.post,
.uploaded-image {
    display: flex;
    flex-direction: column;
    width: 600px;
    margin: 20px 0;
    background-color: #f7f7f7;
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.post:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.2);
}

.post img {
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.post-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.username {
    font-weight: bold;
    font-size: 1.2rem;
}

.post-footer {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.likes {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 10px;
}

.caption {
    font-size: 1.2rem;
    margin-bottom: 10px;
}

.comments {
    font-size: 1rem;
    color: rgb(0, 0, 0);
    margin-bottom: 10px;
}

.comment {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 5px;
}

.comment .username {
    font-weight: bold;
    margin-right: 5px;
}

.comment .text {
    color: rgb(0, 0, 0);
}

.comment-form {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 10px;
}

.comment-form input {
    flex: 1 1;
    border: none;
    background-color: #eee;
    padding: 10px;
    border-radius: 20px;
    margin-right: 10px;
}

.comment-form button {
    border: none;
    background-color: rgb(190, 97, 21);
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
}

.comment-form button:hover {
    background-color: rgb(249, 160, 86);
}

.upload-container {
    /* position: absolute;
    top: 20px;
    left: 50%; */
    /* transform: translateX(-50%); */
    margin: auto;
    border: none;
    background-color: #001a37;
    color: #fff;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
    /* width: 70px;
    height: 70px; */
}

.upload-container:hover {
    background-color: #333;
    color: #fff;
}

.upload-label {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 1rem;
    font-weight: bold;
    color: #333;
    text-align: center;
    cursor: pointer;
    z-index: -1;
}




.auth {
    text-align: center;
    background-color:  rgb(1, 11, 57);
    margin: auto;
    padding: 5em;
    display: flex;
    /* flex-direction: row; */
    vertical-align: center;
    justify-content: space-around;
}

.logo__container {
position: relative;
align-content: center;
}

.mainlogo{
    position: relative;
    border-radius: 300% 0px 300% 300%;
    scale: 70%;
    margin: auto;
}

.welcome {
    position: absolute;
    left: 150px;
    top: 100px;
    display: flex;
    flex-direction: column;
    width: 200px;
    height: 200px;
    border-radius: 50%;
background-color: rgb(190, 97, 21);
/* box-shadow: 0px 0px 20px 20px rgb(255, 183, 0); */
color: white;
-webkit-animation: shine 3s ease-in-out 2s 1 normal forwards;
        animation: shine 3s ease-in-out 2s 1 normal forwards;
}

@-webkit-keyframes shine {
    0% {
        box-shadow: 0px 0px 0px 0px rgb(255, 183, 0);
    }

    100% {
        box-shadow: 0px 0px 100px 100px rgb(255, 183, 0);
        -webkit-transform: rotateZ(-40deg);
                transform: rotateZ(-40deg);
        left: 20px;
        top: 40px;
    }
}

@keyframes shine {
    0% {
        box-shadow: 0px 0px 0px 0px rgb(255, 183, 0);
    }

    100% {
        box-shadow: 0px 0px 100px 100px rgb(255, 183, 0);
        -webkit-transform: rotateZ(-40deg);
                transform: rotateZ(-40deg);
        left: 20px;
        top: 40px;
    }
}





.login {
    display: flex;
    flex-direction: column;
}

 /* .infoInput {
    margin: .25em;
    padding: 18px;
    border-radius: 20px;
    background-color: white;
    color: black;

} */

.infoInput:focus {
    background-color: black;
    color: white;
} 

.loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.login__button {
    margin: .75em;
    padding: .75em;
    border-radius: 10px;
    font-weight: boldest;
    font-size: 1em;
    cursor: pointer;
}

.login__button:hover {
    background-color: rgb(190, 97, 21);
   box-shadow: 0px 0px 5px 5px rgba(138, 158, 255, 0.8);
    color: white;
    transition: all  .5s;
}


.signUpBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 20px;
    color: white;
    font-weight: boldest;
    font-size: auto;
    margin: 2em auto;
}

.signUpBox a {
    text-decoration: none;
    color: white;
    z-index: 0;
}

.signUpBox a:hover {
    text-shadow: 3px 3px 20px rgb(255, 183, 0);
font-weight: boldest;  
scale: 115%;  
z-index: 3;
color: gold;
transition: all 1s;
}

@media(max-width: 850px) {
    .auth {
        flex-direction: column;
        align-content: center;
        margin: auto;
    }
    .logo__container {
        margin: auto;
        scale: 85%;
    }
}

@media(max-width: 700px) {
    .auth {
        flex-direction: column;
        margin: auto;
        align-items: center;
        align-content: center;
        text-align: center;
        padding: 0;
    }

    .logo__container {
        margin: 1em;
        scale: 60%;
    }

}

@media(max-width: 400px) {

    .auth {
        padding: 0em;
        margin: auto;
    }

    .logo__container {
scale: 50%;  max-height: 300px;  }



.welcome {
    scale: 90%;
}

}

@media(max-width: 300px) {

.logo__container{
    display: none;
}

    .welcome{
       display: none;
    }
.login {
    vertical-align: center;
    margin: 10em auto;
}

}


